#home {
	position: relative;
	background: url(../../assests/bgIMG.png);
	background-size: cover;
	background-repeat: repeat;
	background-position: center;
	.app__wrapper {
		padding: 0;
	}
}

.app__header {
	flex: 1;
	width: 100%;
	height: 100%;
	flex-direction: row;
	padding: 6rem 2rem 0;

	@media screen and (min-width: 2000px) {
		padding-top: 8rem;
	}
	// for mobiles
	@media screen and (max-width: 1200px) {
		flex-direction: column;
	}
	@media screen and (max-width: 450px) {
		padding: 6rem 1rem 2rem;
	}
}

.app__header-info {
	flex: 0.65;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	height: 100%;
	@media screen and (max-width: 2000px) {
		width: 100%;
		margin-right: 0px;
	}
}

.app__header-hero {
	width: 100%;
	margin-bottom: 2rem;
	
	.hero-text {
		font-size: 3rem;
		font-weight: 800;
		line-height: 1.1;
		color: var(--black-color);
		text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
		margin: 0;
		max-width: 600px;
		
		@media screen and (min-width: 2000px) {
			font-size: 4.5rem;
			max-width: 800px;
		}
		
		@media screen and (max-width: 1200px) {
			font-size: 2.5rem;
			text-align: center;
			max-width: 100%;
		}
		
		@media screen and (max-width: 450px) {
			font-size: 1.8rem;
		}
	}
}

.app__header-cta {
	margin-top: 2rem;
	
	.cta-button {
		display: flex;
		align-items: center;
		gap: 10px;
		padding: 1rem 2rem;
		background: var(--black-color);
		color: var(--white-color);
		border: 2px solid var(--black-color);
		border-radius: 25px;
		font-size: 1.1rem;
		font-weight: 600;
		cursor: pointer;
		transition: all 0.3s ease;
		box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
		text-decoration: none;
		
		&:hover {
			background: var(--white-color);
			color: var(--black-color);
			transform: translateY(-2px);
			box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
			text-decoration: none;
		}
		
		&:visited {
			color: var(--white-color);
		}
		
		&:visited:hover {
			color: var(--black-color);
		}
		
		.cta-arrow {
			font-size: 1.2rem;
			transition: transform 0.3s ease;
		}
		
		&:hover .cta-arrow {
			transform: translateX(5px);
		}
		
		@media screen and (max-width: 450px) {
			padding: 0.8rem 1.5rem;
			font-size: 1rem;
		}
	}
}

.app__header-badge {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	flex-direction: column;
	margin-top: 1rem;
	
	.badge-cmp,
	.tag-cmp {
		padding: 1rem 2rem;
		border: var(--white-color);
		border-radius: 15px;
		box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
		width: auto;
		background: rgba(255, 255, 255, 0.9);
		backdrop-filter: blur(10px);
		
		span {
			font-size: 2rem;
		}
	}
	.tag-cmp {
		flex-direction: column;
		margin-top: 1rem;
		p {
			width: 100%;
			text-align: right;
		}
	}
	@media screen and (max-width: 1200px) {
		justify-content: flex-start;
		align-items: flex-start;
	}
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

    img {
      width: 60%;
      height: 60%;
    }
  }

  // circles with different height & width

  div:nth-child(1) {
    width: 100px;
    height: 100px;
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
  }

  div:nth-child(3) {
    width: 70px;
    height: 70px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(3) {
      width: 170px;
      height: 170px;
    }

    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }
}